import { useEffect, useRef } from "react";
import Helmet from "react-helmet"

export type SeoSettingsType = {
    title?: string,
    description?: string,
    keywords?: string,
    author?: string,
    image?: string | any,
    locale?: string,
    siteName?: string,
    snippetData?: any;
    video?: string;
    type?: string;
    publishedTime?: string;
    modifiedTime?: string;
    section?: string;
    tag?: string;
    structralData?: any;
    breadcrumb?: any;
}
export const DefaultSeoSettings: SeoSettingsType = {
    title: "Can Gökçeaslan | Kurucu Ortak & FullStack Developer",
    description: "Merhabalar, ben Can Gökçeaslan. Şu an bir girişimci ve online eğitmenim. Benimle ilgili daha fazla bilgiye sahip olabilmek için lütfen sayfayı ziyaret edin.",
    keywords: "cangokceaslan, Can Gökçeaslan, Can Gokceaslan, CanGokceaslan, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim",
    author: "Can Gökçeaslan",
    image: "https://www.cangokceaslan.com/assets/jpg/courses/Iyzico.jpg?cache_delete=1",
    locale: "tr",
    siteName: "https://www.cangokceaslan.com",
    type: "website"
}
const DocumentSettings = ({
    docData = DefaultSeoSettings
}) => {
    const helmet = useRef(null);
    const image = "/assets/jpg/social.jpg";

    useEffect(() => {
        if (docData?.structralData !== undefined) {
            const script = document.createElement('script');
            script.type = 'application/ld+json';
            script.innerHTML = JSON.stringify(docData?.structralData);
            document.head.appendChild(script);
        }
        if (docData?.breadcrumb !== undefined) {
            const script = document.createElement('script');
            script.type = 'application/ld+json';
            script.innerHTML = JSON.stringify(docData?.breadcrumb);
            document.head.appendChild(script);
        }
    }, [])

    return (
        <Helmet defer={false} ref={helmet}>
            <title>{docData?.title}</title>
            <meta name="description" content={docData?.description} />
            <meta name="keywords" content={docData?.keywords} />
            <meta name="author" content={docData?.author} />
            <meta name="image" content={docData?.image || image} />
            <meta name="locale" content={docData?.locale} />
            <meta name="siteName" content={docData?.siteName} />
            {docData?.video && <meta name="media:video" content={docData?.video} />}
            {docData?.video && <a href={docData?.video} style={{ display: "none" }} type="video" target="_blank" rel="noopener noreferrer"></a>}
            {!docData?.video && <meta name="type" content={docData?.type ? docData?.type : "website"} />}
            {docData?.video && <meta name="type" content="video" />}
            <meta name="og:title" content={docData?.title} />
            <meta name="og:description" content={docData?.description} />
            <meta name="og:image" content={docData?.image || image} />
            <meta name="og:locale" content={docData?.locale} />
            <meta name="og:site_name" content={docData?.siteName} />
            <meta name="og:type" content="website" />
            <meta name="og:author" content={docData?.author} />
            <meta name="og:type" content={docData?.type ? docData?.type : "website"} />
            {docData?.video && <meta name="og:video" content={docData?.video} />}
            {docData?.video && <meta property="og:video:secure_url" content={docData?.video} />}
            {docData?.video && <meta property="og:video:type" content="text/html" />}
            <meta name="twitter:title" content={docData?.title} />
            <meta name="twitter:description" content={docData?.description} />
            <meta name="twitter:image" content={docData?.image || image} />
            <meta name="twitter:site" content={docData?.siteName} />
            <meta name="twitter:creator" content={docData?.author} />
            {docData?.video ? <meta name="twitter:card" content="player" /> : <meta name="twitter:card" content="summary_large_image" />}
            {docData?.video && <meta name="twitter:player" content={docData?.video} />}
            {docData?.video && <meta name="twitter:video" content={docData?.video} />}
            <meta name="twitter:type" content={docData?.type ? docData?.type : "website"} />
            {docData?.publishedTime && <meta name="article:published_time" content={docData?.publishedTime} />}
            {docData?.modifiedTime && <meta name="article:modified_time" content={docData?.modifiedTime} />}
            {docData?.section && <meta name="article:section" content={docData?.section} />}
            {docData?.tag && <meta name="article:tag" content={docData?.tag} />}
        </Helmet>
    )
}

export default DocumentSettings;